html {
  -ms-overflow-style: -ms-autohiding-scrollbar;
  text-size-adjust: 100%;
}

main {
  display: block;
}

details {
  display: block;
}

pre {
  overflow: auto;
}

progress {
  display: inline-block;
}

summary {
  display: block;
}

template {
  display: none;
}

*,
::before,
::after {
  border-style: solid;
  border-width: 0;
  box-sizing: border-box;
}

* {
  font-size: inherit;
  line-height: inherit;
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

audio :not([controls]) {
  display: none;
}

[aria-busy=true] {
  cursor: progress;
}

[aria-controls] {
  cursor: pointer;
}

[aria-disabled] {
  cursor: default;
}

[hidden] {
  display: none;
}

button {
  overflow: visible;
}

input {
  border-radius: 0;
  line-height: normal;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: button;
  cursor: pointer;
}
button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border: 0 none !important;
  padding: 0 !important;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number] {
  width: auto;
}

[type=search] {
  appearance: textfield;
}

button,
input,
select,
textarea {
  background-color: transparent;
  color: inherit;
  font-family: inherit;
  font-style: inherit;
  font-weight: inherit;
}

textarea {
  overflow: auto;
  resize: vertical;
}

img {
  border: 0;
}

svg:not(:root) {
  overflow: hidden;
}

audio,
canvas,
iframe,
img,
svg,
video {
  max-width: 100%;
  vertical-align: middle;
}

a {
  background-color: transparent;
}
a:active, a:hover {
  outline: 0;
}

abbr[title] {
  border-bottom: 1px dotted;
}

b,
strong {
  font-weight: bold;
}

code,
kbd,
pre,
samp {
  font-family: monospace;
  font-size: inherit;
}

dfn {
  font-style: italic;
}

nav ol,
nav ul {
  list-style: none;
}

small {
  font-size: 0.75em;
}

sub,
sup {
  font-size: 0.75em;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -0.5em;
}

sub {
  bottom: -0.25em;
}

[data-theme=light] {
  --color-brand-blue: #266478;
  --color-brand-orange: #ff8b3d;
  --color-brand-pink: #e60073;
  --color-blue-light: #49a6c5;
  --color-blue-dark: #215869;
  --color-pink-light: #ff57ab;
  --color-pink-dark: #b10d65;
  --color-gray-dark: #3f3f40;
  --color-gray: #4b4c4d;
  --color-gray-light: #a3a4a6;
  --color-contrast-light: #fafcff;
  --color-contrast-dark: #13323c;
  --color-added-dark: #3d6644;
  --color-added-light: #d8eedb;
  --color-changed-dark: #3e6a79;
  --color-changed-light: #e0f7ff;
  --color-removed-dark: #946ca7;
  --color-removed-light: #d0b9da;
  --color-background:var(--color-contrast-light, #fafcff);
  --color-text:var(--color-contrast-dark, #13323c);
  --color-text-light:var(--color-gray-dark, #3f3f40);
  --color-border:var(--color-gray, #4b4c4d);
  --color-border-light:var(--color-gray-light, #a3a4a6);
  --color-action:var(--color-brand-blue, #266478);
  --color-action-hover:var(--color-pink-dark, #b10d65);
  --color-action-bright:var(--color-brand-pink, #e60073);
  --color-added-fg:var(--color-added-dark, #3d6644);
  --color-added-bg:var(--color-added-light, #d8eedb);
  --color-changed-fg:var(--color-changed-dark, #3e6a79);
  --color-changed-bg:var(--color-changed-light, #e0f7ff);
  --color-removed-fg:var(--color-removed-dark, #946ca7);
  --color-removed-bg:var(--color-removed-light, #d0b9da);
}

[data-theme=dark] {
  --color-brand-blue: #266478;
  --color-brand-orange: #ff8b3d;
  --color-brand-pink: #e60073;
  --color-blue-light: #49a6c5;
  --color-blue-dark: #215869;
  --color-pink-light: #ff57ab;
  --color-pink-dark: #b10d65;
  --color-gray-dark: #3f3f40;
  --color-gray: #4b4c4d;
  --color-gray-light: #a3a4a6;
  --color-contrast-light: #fafcff;
  --color-contrast-dark: #13323c;
  --color-added-dark: #3d6644;
  --color-added-light: #d8eedb;
  --color-changed-dark: #3e6a79;
  --color-changed-light: #e0f7ff;
  --color-removed-dark: #946ca7;
  --color-removed-light: #d0b9da;
  --color-background:var(--color-contrast-dark, #13323c);
  --color-text:var(--color-contrast-light, #fafcff);
  --color-text-light:var(--color-gray-light, #a3a4a6);
  --color-border:var(--color-gray, #4b4c4d);
  --color-border-light:var(--color-gray-dark, #3f3f40);
  --color-action:var(--color-blue-light, #49a6c5);
  --color-action-hover:var(--color-pink-light, #ff57ab);
  --color-action-bright:var(--color-brand-pink, #e60073);
  --color-added-fg:var(--color-added-light, #d8eedb);
  --color-added-bg:var(--color-added-dark, #3d6644);
  --color-changed-fg:var(--color-changed-light, #e0f7ff);
  --color-changed-bg:var(--color-changed-dark, #3e6a79);
  --color-removed-fg:var(--color-removed-light, #d0b9da);
  --color-removed-bg:var(--color-removed-dark, #946ca7);
}

[data-theme] {
  background-color: var(--color-background, #fafcff);
  color: var(--color-text, #13323c);
}

:root {
  font-family: freight-text-pro, Baskerville, Palatino, Cambria, Georgia, serif;
  font-size: calc(16px + 0.25vw);
}

h1,
h2,
h3 {
  font-family: freight-sans-pro, "Helvetica Neue", Helvetica, Arial, sans-serif;
  margin: 1em 0 0.5em;
}

h1 {
  font-size: 1.96rem;
}

h2 {
  font-size: 1.4rem;
}

p {
  margin-bottom: 1em;
}

.is-hidden {
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(1px 1px 1px 1px);
  height: 1px;
  overflow: hidden;
  pointer-events: none;
  position: absolute;
  width: 1px;
}

[data-layout=grid] {
  display: grid;
  grid-gap: 4.2rem 1.4rem;
  grid-template: "banner graphic" auto "main   graphic" 1fr "footer graphic" auto/minmax(0, 1fr) 0;
  justify-items: center;
  min-height: 100vh;
  padding: 1.4rem;
}
@media (min-width: 52.5em) {
  [data-layout=grid] {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}

[data-layout=banner],
[data-layout=main],
[data-layout=footer] {
  width: 100%;
  max-width: 35rem;
}

[data-layout=banner] {
  grid-area: banner;
}

[data-layout=main] {
  grid-area: main;
}

[data-layout=graphic] {
  background-image: radial-gradient(circle at 0 50%, var(--color-changed-bg, #e0f7ff), transparent);
  grid-area: graphic;
  justify-self: stretch;
  margin: -1.4rem -1.4rem -1.4rem 0;
}

[data-layout=footer] {
  grid-area: footer;
}

[data-icon=logo] {
  width: 14em;
}

[data-logo=odd] {
  fill: var(--color-action-bright, #e60073);
}

[data-logo=books] {
  fill: var(--color-action, #266478);
}

[data-field] {
  margin-bottom: 0.5em;
}

[type=text],
[type=email] {
  border: 1px solid;
}

[type=submit] {
  border: 1px solid;
  border-radius: 0.25em;
  padding: 0.25em 1em;
}